.skills-container {
  row-gap: 0;
}
.skills-header {
  display: flex;
  align-items: center;
  margin-bottom: 2.5rem;
  cursor: pointer;
}
.skills-content i {
  margin-left: auto;
  transition: 0.4s;
  color: #e73f3f;
  font-size: 2rem;
}
.skills-content {
  margin-bottom: 20px;
}

.skills-content-front,
.skills-content-back {
  margin: auto;
}

.skills-list {
  row-gap: 1.5rem;
  padding-left: 2.7rem;
}
.skills-title {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
.skills-name {
  font-size: 1.2rem;
  font-weight: 500;
}

.skills-bar,
.skills-percentage {
  height: 8px;
  border-radius: 0.25rem;
}
.skills-bar {
  background-color: rgb(22, 22, 22);
}
.skills-bar.dark-mode {
  background-color: #b8b6b6;
}

.skills-percentage {
  display: block;
  background-color: #e73f3f;
}

.skills-html {
  width: 90%;
}
.skills-css {
  width: 70%;
}
.skills-js {
  width: 70%;
}
.skills-react {
  width: 60%;
}
.skills-redux {
  width: 40%;
}
.skills-query {
  width: 40%;
}
.skills-node {
  width: 90%;
}
.skills-express {
  width: 80%;
}
.skills-mongo {
  width: 70%;
}
.skills-sql {
  width: 70%;
}
.skills-post {
  width: 50%;
}

.skills-close .skills-list {
  height: 0;
  overflow: hidden;
}
.skills-open .skills-list {
  height: max-content;
  margin-bottom: 2.5rem;
}

.skills-open .skills-arrow {
  transform: rotate(-180deg);
}
